@font-face {
  font-family: 'PlayfairDisplay Regular';
  src: url("../fonts/PlayfairDisplay-Regular.ttf");
}

@font-face {
  font-family: 'Ubuntu-Light';
  src: url("../fonts/Ubuntu-Light.ttf");
}

@font-face {
  font-family: 'PlayfairDisplay Italic';
  src: url("../fonts/PlayfairDisplay-Italic.ttf");
}

@font-face {
  font-family: 'PlayfairDisplay Black';
  src: url("../fonts/PlayfairDisplay-Black.ttf");
}

@font-face {
  font-family: 'PlayfairDisplay Bold';
  src: url("../fonts/PlayfairDisplay-Bold.ttf");
}

@font-face {
  font-family: 'Inconsolata-Regular';
  src: url("../fonts/Inconsolata-Regular.ttf");
}


body {
  margin: 0 auto;
  padding: 0;
  width: 1200px;
}

.header {
  max-width: 1063px;
  height: 150px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;

  .header_name {
    margin: 70px 0 0 0;
    font-size: 30px;
    font-family: 'Inconsolata-Regular', serif;
    padding-right: 8px;
  }

  .header_button {
    display: none;
    position: relative;
    left: -10px;
    background-color: transparent;
    background-image: url("../img/header/icon.png");
    border: none;
    outline: none;
  }


  .header_nav {
    margin: 70px 0 0 0;
    display: flex;
    justify-content: flex-end;

    ul {
      margin-top: 10px;
      width: auto;

      li {
        list-style: none;
        float: left;
        margin-left: 96px;
        color: #626262;
        font-size: 14px;
        font-family: 'PlayfairDisplay Regular', serif;
      }

      li:hover {
        cursor: pointer;
      }
    }
  }
}


main {
  max-width: 1063px;
  width: 100%;
  margin: 0 auto;


  .main_img {
    margin: 0 auto 99px auto;
  }
}

.article-photodairy {
  width: 902px;
  margin: 0 auto;

  h3 {
    margin: 0 0 28px 0;
    font-size: 14px;
    font-family: 'Ubuntu-Light', sans-serif;
    font-weight: normal;
    color: #c7c1b6;
  }

  h2 {
    margin: 0 0 34px 0;
    font-size: 30px;
    font-family: 'PlayfairDisplay Regular', serif;
    color: #626262;
  }

  p {
    margin: 0 0 52px 0;
    font-size: 14px;
    font-family: 'PlayfairDisplay Regular', serif;
    color: #626262;

    span {
      font-family: 'PlayfairDisplay Bold', serif;
    }
  }
}

.info {
  width: 902px;
  margin: 105px auto 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  .info_block {
    width: 420px;
    margin-bottom: 123px;

    .image {
      overflow: hidden;
      width: 421px;
      height: 281px;

      img {
        width: 421px;
        height: 281px;
        transition: all 1s ease-out;
      }

      img:hover {
        transform: scale(1.1);
      }
    }

    h3 {
      margin: 32px 0 29px 0;
      font-size: 14px;
      font-family: 'Ubuntu-Light', sans-serif;
      font-weight: normal;
      color: #bdb7aa;
    }

    h2 {
      margin: 0 0 34px 0;
      font-size: 30px;
      font-family: 'PlayfairDisplay Regular', serif;
      color: #626262;
      font-weight: lighter;

    }

    p {
      margin: 0 0 52px 0;
      font-size: 14px;
      font-family: 'PlayfairDisplay Regular', serif;
      color: #626262;
    }
  }
}

.communication {
  max-width: 1200px;
  width: 100%;
  height: 287px;
  background-color: #f0f0f0;
  display: flex;
  flex-direction: column;
  justify-content: center;

  h2 {
    padding: 82px auto 81px auto;
    margin: 0 auto;
    font-size: 30px;
    font-family: 'PlayfairDisplay Regular', serif;
    color: #626262;
  }

  label {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 80px auto 0 auto;
  }

  input {
    width: 405px;
    height: 30px;
    background-color: #f0f0f0;
    border: none;
    border-bottom: 1px solid black;
    padding-left: 5px;
    font-family: 'PlayfairDisplay Regular', serif;
    font-size: 14px;
  }

  input:invalid {
    background-color: red;
    opacity: 0.5;
  }

  button {
    width: 33px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    position: relative;
    left: -38px;
  }
}

.loading {
  text-decoration: none;
  margin: 0 auto;

  button {
    width: 164px;
    height: 60px;
    background-color: transparent;
    margin: 0 auto;
    font-size: 20px;
    font-family: 'PlayfairDisplay Regular', serif;
    border: 1px solid black;
    color: #6c6c6c;
    outline: none;
  }

  button:hover {
    background-color: #f0f0f0;
    cursor: pointer;
  }
}

/*--------------------------------- FOOTER -----------------------------------*/

footer {
  width: 1200px;
  height: 99px;
  background-color: #f0f0f0;
  margin-top: 90px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  .rules {
    margin: 40px 0 40px 126px;

    ul {
      margin: 0;
    }

    li {
      list-style: none;
      margin-right: 20px;
      float: left;
      font-size: 14px;
      font-family: 'PlayfairDisplay Regular', serif;
    }
  }

  .social {
    margin: 40px 71px 40px 0;

    ul {
      margin: 0;
    }

    li {
      list-style: none;
      margin-right: 20px;
      float: left;
      font-size: 14px;
      font-family: 'PlayfairDisplay Regular', serif;
    }

  }
}

/*-------------------------- MAIN PART OF "SINGLEPOST PAGE" ------------------------------*/

.writing {
  width: 1062px;
  margin-bottom: 116px;
  display: flex;
  justify-content: space-between;

  h3 {
    margin: 32px 0 29px 0;
    font-size: 14px;
    font-family: 'Ubuntu-Light', sans-serif;
    color: #c7c1b6;
  }

  h2 {
    margin: 0 0 34px 0;
    font-size: 30px;
    font-family: 'PlayfairDisplay Regular', serif;
    color: #626262;
  }

  p {
    margin: 0 0 52px 0;
    font-size: 14px;
    font-family: 'PlayfairDisplay Regular', serif;
    color: #626262;
    line-height: 1.5;

    span {
      font-weight: bold;
    }
  }

  /* ----------------------------- LEFT PART OF MAIN CONTAINER (SLIDE BAR) -----------------------*/

  .left {
    margin: 0 0 73px 78px;
    width: 659px;

    .image {
      overflow: hidden;
      width: 660px;
      height: 497px;
      margin-bottom: 50px;


      img {
        width: 660px;
        height: 497px;
        transition: all 1s ease-out;
      }

      img:hover {
        transform: scale(1.1);
      }
    }

    .text {
      margin-bottom: 75px;

      .quote {
        margin: 0 34px 33px 20px;
        font-family: 'PlayfairDisplay Italic', serif;
        font-size: 14px;
        color: #626262;
        line-height: 1.5;
        font-weight: lighter;
      }

      .end {
        margin: 0;
        font-family: 'PlayfairDisplay Regular', serif;
        font-size: 14px;
        line-height: 1.5;
        color: #626262;
        font-weight: lighter;
      }

      span {
        font-family: 'PlayfairDisplay Black', serif;
      }
    }

    .share {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;

      h5 {
        margin: 0;
        display: inline-block;
        font-family: 'Ubuntu-Light', sans-serif;
        font-size: 13px;
        color: #c8c3b8;
        font-weight: lighter;
      }

      img {
        margin-left: 16px;
      }
    }
  }

  /* ------------------------------- RIGHT PART OF MAIN CONTAINER (SLIDE BAR) -----------------------*/

  .slideBar {
    margin-left: 40px;
    width: 281px;

    .firstImg {
      height: 177px;
    }

    h4 {
      font-family: 'PlayfairDisplay Regular', serif;
      font-size: 24px;
      margin: 26px 0 20px 0;
      color: #626262;
      font-weight: normal;
    }

    h5 {
      font-size: 12px;
      font-family: 'PlayfairDisplay Regular', serif;
      margin: 0 0 20px 0;
      color: #626262;
      font-weight: normal;
    }

    .socialNetworks {
      margin-bottom: 74px;
      height: 18px;


      img {
        float: left;
        margin-right: 16px;
        height: 18px;
      }
    }

    h6 {
      margin: 0 0 30px 0;
      font-family: 'Ubuntu-Light', sans-serif;
      font-size: 14px;
      color: #b4ad9e;
      font-weight: normal;
    }

    .list {
      margin: 0;
      padding-left: 15px;
      color: #b4ad9e;
      font-size: 16px;


      span {
        font-family: 'PlayfairDisplay Regular', serif;
        font-size: 16px;
        color: #626262;
        font-weight: normal;
      }

      .comments {
        font-family: 'Ubuntu-Light', sans-serif;
        font-size: 12px;
        margin: 14px 0 30px 0;
        color: #d0ccc2;
        font-weight: normal;
      }
    }

    .banner {
      margin-top: 25px;
      position: relative;
      left: -68px;
    }
  }
}


/* ---------------------------- CONTAINER OF SLIDERS PHOTO ------------------------*/


.actualPhotos {
  max-width: 1200px;
  width: 1200px;
  height: 435px;
  background-color: #f5f5f5;

  h3 {
    margin: 0;
    padding: 70px 0 40px 150px;
    color: #b4ad9e;
    font-family: 'Ubuntu-Light', sans-serif;
    font-size: 14px;
    font-weight: normal;
  }

  .photos {
    display: flex;
    flex-direction: row;
    justify-content: center;

    .container {
      width: 275px;
      margin: 0 20px;

      h4 {
        margin: 24px 0 0 0;
        color: #747474;
        font-family: 'PlayfairDisplay Regular', serif;
        font-size: 20px;
        font-weight: normal;
      }

      #specialColor {
        color: #b4ad9e;
      }
    }
  }
}

/* ---------------------------- CONTAINER OF COMMENTS --------------------------------------*/


.comment {
  width: 900px;
  margin: 0 auto;

  h4 {
    margin: 66px 0 44px 0;
    color: #b4ad9e;
    font-family: 'Ubuntu-Light', sans-serif;
    font-size: 14px;
    font-weight: normal;
  }

  .usersComment {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin: 0 auto 56px auto;


    .img {
      width: 70px;
      margin-right: 35px;
    }

    .commentText {

      h3 {
        font-family: 'PlayfairDisplay Bold', serif;
        font-size: 14px;
        color: #626262;
        margin: 0 0 15px 0;
      }

      .usersText {
        color: #626262;
        font-family: 'PlayfairDisplay Regular', serif;
        font-size: 14px;
        font-weight: normal;
      }
    }
  }

  .reply {
    margin-top: 20px;
    color: #b4ad9e;
    font-family: 'Ubuntu-Light', sans-serif;
    font-size: 14px;
  }

  input {
    width: calc(793px - 33px);
    height: 69px;
    padding-left: 33px;
    border: 1px solid #c8c8c8;
    border-radius: 6px;
    font-family: 'Ubuntu-Light', sans-serif;
    font-size: 14px;
    color: #c8c8c8;
  }
}

.connect {
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;

  h5 {
    margin: 0;
    display: inline-block;
    font-family: 'Ubuntu-Light', sans-serif;
    font-size: 13px;
    color: #c8c3b8;
    font-weight: normal;
  }

  img {
    margin-left: 16px;
  }

}

